import './style.css'
import * as THREE from 'three'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader.js'
import { gsap } from 'gsap'
import { IglooRenderer } from './IglooRendererSVBMod.js'
import { text } from 'troika-three-text'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'


/**
 * Base
 */

// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()
scene.background = new THREE.Color( '#87CEFA' )
scene.fog = new THREE.Fog( '#ffffff', 0.1, 1500 )


/**
 * Sizes
 */
 const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

/**
 * Set the coordinates that will be used for the camera
 * 
 * Assign a starting location only for the start of the scene
 * so that we can put information on how to use the app, timer, etc
 * 
 * Afterwards, when users press PageUp / PageDown buttons, the camera will change from loc0 to loc...
 * but should never visit the starting location again
 */
const startingLoc = new THREE.Vector3(-20,25,150)
 const loc0 = new THREE.Vector3(-20,25,0)
 const loc1 = new THREE.Vector3(30,25,-100)
 const loc2 = new THREE.Vector3(200,25,-70)
 const loc3 = new THREE.Vector3(230,25,20)
 const loc4 = new THREE.Vector3(100,25,40)
 
const locations = [loc0, loc1, loc2, loc3, loc4]
 
var currectLocIndex = -1 //index is ever -1 at the start of the scene, afterwards it will be 0 to max only
const maxLocLength = locations.length - 1
 
 
 
 /**
  * Camera
  */
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 1000)
camera.position.copy(startingLoc)
 
// create a blank 3D object as an object to follow (only necessary for IglooRenderer)
const objectToFollow = new THREE.Object3D
objectToFollow.position.copy(startingLoc)
scene.add( objectToFollow )


//Orbit controls
const controls = new OrbitControls(camera, canvas)
controls.target.set(200,3,-20)
//controls.enableDamping = true



 /**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas
})
renderer.shadowMap.enabled = true
renderer.shadowMap.type = THREE.PCFSoftShadowMap
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
renderer.outputEncoding = THREE.sRGBEncoding

var igloo = new IglooRenderer(camera, renderer, scene, objectToFollow)


window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

    igloo.update()
})

/**
 * Lights
 */

 const ambientLight = new THREE.AmbientLight(0xffffff, 0.1)
 scene.add(ambientLight)
 
 const directionalLight = new THREE.DirectionalLight(0xffffff, 0.6)
 directionalLight.position.set(-5, 5, 0)
 scene.add(directionalLight)
 
 const hemisphereLight = new THREE.HemisphereLight( 0xffffbb, 0x080820, 0.5 )
 hemisphereLight.position.set(0,10,0)
 scene.add( hemisphereLight )
 

 /**
 * 3D scene
 */
const loadingManager = new THREE.LoadingManager()

var loader = new GLTFLoader(loadingManager)

const dracoLoader = new DRACOLoader()
dracoLoader.setDecoderPath('draco/')
loader.setDRACOLoader(dracoLoader)

loader.load(
    'models/lux-home.glb',
    (gltf) =>
    {
        gltf.scene
        gltf.scene.scale.set(20,20,20)
        scene.add(gltf.scene)
    }
)


/**
 * Models
 */
const items = ['headphones','skunk','duck','ball','axe','dog','flag','targetstand','bucket','umbrella','hammer','banana','cake','knifeblock','loaf','bear','plant','plushie','tea','wine']
const path = 'models/items/'
const fileType = '.gltf'

const xArray = [-35,-60,-110,-120,40,-30,100,30,220,260,260,196,255,265,265,130,43,70,81,88]
const yArray = [7,10,0,3,3,3,3,0,3,3,5,13,21,22,22,3,5,10,16,16]
const zArray = [45,50,5,-30,-25,-130,-80,-170,-140,-100,-25,48,-10,20,30,-10,40,0,70,70]

//First group of items

let group0 = new THREE.Group()
let group0ItemLocs = []

for (let i=0; i<4; i++) {
    
    group0ItemLocs.push(new THREE.Vector3(xArray[i],yArray[i],zArray[i]))

    loader.load(
        path+items[i]+fileType,
        (gltf) =>
        {   
            const model = gltf.scene
            model.scale.set(2,2,2)
            model.position.set(group0ItemLocs[i].x,group0ItemLocs[i].y,group0ItemLocs[i].z)
            model.name = items[i]
            group0.add(model)
        }
    )
}

//Second group of items

let group1 = new THREE.Group()
let group1ItemLocs = []

for (let i=4; i<8; i++) {
    group1ItemLocs.push(new THREE.Vector3(xArray[i],yArray[i],zArray[i]))

    loader.load(
        path+items[i]+fileType,
        (gltf) =>
        {   
            const model1 = gltf.scene
            model1.scale.set(5,5,5)
            model1.position.set(group1ItemLocs[i-4].x,group1ItemLocs[i-4].y,group1ItemLocs[i-4].z)
            model1.name = items[i]
            group1.add(model1)
        }
    )
}

//Third group of items

let group2 = new THREE.Group()
let group2ItemLocs = []

for (let i=8; i<11; i++) {
    group2ItemLocs.push(new THREE.Vector3(xArray[i],yArray[i],zArray[i]))

    loader.load(
        path+items[i]+fileType,
        (gltf) =>
        {   
            const model2 = gltf.scene
            model2.scale.set(7,7,7)
            model2.position.set(group2ItemLocs[i-8].x,group2ItemLocs[i-8].y,group2ItemLocs[i-8].z)
            model2.name = items[i]
            group2.add(model2)
        }
    )
}

//Fourth group of items
let group3 = new THREE.Group()
let group3ItemLocs = []

for (let i=11; i<15; i++) {
    group3ItemLocs.push(new THREE.Vector3(xArray[i],yArray[i],zArray[i]))

    loader.load(
        path+items[i]+fileType,
        (gltf) =>
        {   
            const model3 = gltf.scene
            model3.scale.set(7,7,7)
            model3.position.set(group3ItemLocs[i-11].x,group3ItemLocs[i-11].y,group3ItemLocs[i-11].z)
            model3.name = items[i]
            group3.add(model3)
        }
    )
}

//Fifth group of items
let group4 = new THREE.Group()
let group4ItemLocs = []

for (let i=15; i<20; i++) {
    group4ItemLocs.push(new THREE.Vector3(xArray[i],yArray[i],zArray[i]))

    loader.load(
        path+items[i]+fileType,
        (gltf) =>
        {   
            const model4 = gltf.scene
            model4.scale.set(7,7,7)
            model4.position.set(group4ItemLocs[i-15].x,group4ItemLocs[i-15].y,group4ItemLocs[i-15].z)
            model4.name = items[i]
            group4.add(model4)
        }
    )
}

scene.add(group0,group1,group2,group3,group4)

const groups = [group0,group1,group2,group3,group4]



/**
* Item Indicator
*/

const edges = new THREE.EdgesGeometry( new THREE.BoxGeometry( 12,12,12 ) )
const boxLine = new THREE.LineSegments( edges, new THREE.LineBasicMaterial( { color: '#0FFF50', linewidth: 5} ) )
boxLine.position.set(startingLoc.x,startingLoc.y,startingLoc.z)
//scene.add( boxLine )

var activeGroup
var activeGroupNumItems

var activeItemIndex = 0

var target = new THREE.Vector3()
var activeItem


/**
* Zoom panels
*/
const zoomImgPath = 'textures/zooms/'
const imgType = '.png'

var activeItemZoom

const zoomPanel = new THREE.Mesh(
    new THREE.PlaneGeometry(8,6),
    new THREE.MeshBasicMaterial(
        {map: new THREE.TextureLoader().load(zoomImgPath + 'duck' + imgType)}
    )
)
zoomPanel.rotation.y = THREE.Math.degToRad( 90 )
zoomPanel.visible = false

scene.add(zoomPanel)

var zoomPanelOn = false


/**
* Placard (or Bag)
*/
let placard = new THREE.Object3D()

loader.load(
    'models/items/bag.gltf',
    (gltf) =>
    {
        placard = gltf.scene
        placard.scale.set(12,12,12)
    }
)


/**
* Inventory
*/
// a list to store the items' names

const previewImgPath = 'textures/previews/'

const inventoryList = []
var inventoryNum = inventoryList.length

const inventorySlots = new THREE.Group()

// create the slots to display item's pictures
for (let p=0; p<10; p++) {
    const slot = new THREE.Mesh(
        new THREE.PlaneGeometry(0.7,0.7),
        new THREE.MeshBasicMaterial(
            {color: 'black'}
        )
    )
    slot.position.x = -3.8 + p*0.85
    slot.position.z = 0.01 //position the slots to be slightly closer to the camera
    inventorySlots.add(slot)
}

const inventoryBG = new THREE.Mesh(
    new THREE.PlaneGeometry(9,1),
    new THREE.MeshBasicMaterial(
        {color: 'white', transparent: true, opacity: 0.8}
    )
)

//marker to select inventory item
const inventoryMarker = new THREE.Mesh(
    new THREE.PlaneGeometry(0.85,0.85),
    new THREE.MeshBasicMaterial(
        {color: 'lime'}
    )
)
inventoryMarker.position.x = inventorySlots.children[0].position.x
inventoryMarker.position.z = 0.005 //position the marker to be in between slots and inventoryBG

var invSelectionIndex = 0

//create a bigger group so we can manage animation in one go
const inventoryGroup = new THREE.Group()
inventoryGroup.add(inventorySlots, inventoryBG, inventoryMarker)

inventoryGroup.position.y = camera.position.y+5.5

scene.add(inventoryGroup)

var inventoryIsUp = false



/**
* Object to Observe
*/
let observeItem = new THREE.Object3D()

var observationMode = false


/**
* Overlay panel
* will semi-block the scene whenever users call for inventory panel, zoom panel, or are in observation mode
*/
const overlay = new THREE.Mesh(
    new THREE.BoxGeometry(50,50,50),
    new THREE.MeshBasicMaterial(
        {color: 'black', transparent: true, opacity: 0.6, side: THREE.DoubleSide}
    )
)

scene.add(overlay)

/**
 * Update
 */
//const clock = new THREE.Clock()
//let previousTime = 0

document.addEventListener( 'keydown', onKeyDown )

function update() {

    //We put this in update so that the panels will always follow camera's position when it moves from place to place
    zoomPanel.position.set(camera.position.x-10,camera.position.y,camera.position.z)
    inventoryGroup.position.x = camera.position.x
    inventoryGroup.position.z = camera.position.z-10

    observeItem.position.set(camera.position.x,camera.position.y-2,camera.position.z-10)

    overlay.position.set(camera.position.x,camera.position.y,camera.position.z)
    
    if (zoomPanelOn || inventoryIsUp || observationMode) {
        overlay.visible = true
    } else {
        overlay.visible = false
    }

    // Update controls -- only for dev, it should be disabled for 360imx build
    controls.update()

    if (igloo.render() != true) {
        renderer.render( scene, camera )
    }
    // Update every frame
    window.requestAnimationFrame(update)
}

update()

function onKeyDown( event ) {

if (!inventoryIsUp && !observationMode) {

    switch (event.keyCode)
    {
        case 33: //pageUp key
            if (currectLocIndex === maxLocLength) {
                currectLocIndex = 0
            } else {
                currectLocIndex++ 
            }
            gsap.to(camera.position, { x: locations[currectLocIndex].x, z: locations[currectLocIndex].z ,ease: "circle", duration: 7 })
            gsap.to(objectToFollow.position, { x: locations[currectLocIndex].x, z: locations[currectLocIndex].z ,ease: "circle", duration: 7 })

            activeGroup = groups[currectLocIndex]
            activeGroupNumItems = activeGroup.children.length

            // boxLine (selection marker) will only show up if there are 1 or more items in the group
            if (activeGroupNumItems > 0) {
                scene.add(boxLine)
                activeItemIndex = 0 //reset active item index to 0 (first child of the group)

                activeItem = activeGroup.children[activeItemIndex]
                activeItem.getWorldPosition(target)
                //console.log(activeItem.name, target)
                gsap.to(boxLine.position, { x: target.x, y: target.y, z: target.z, ease: "circle"})
            } else {
                scene.remove(boxLine)
            }
            
            break

        case 34: //pageDown key
            if (currectLocIndex === 0) {
                currectLocIndex = maxLocLength
            } else {
                currectLocIndex--
            }
            gsap.to(camera.position, { x: locations[currectLocIndex].x, z: locations[currectLocIndex].z ,ease: "circle", duration: 7 })
            gsap.to(objectToFollow.position, { x: locations[currectLocIndex].x, z: locations[currectLocIndex].z ,ease: "circle", duration: 7 })
            
            activeGroup = groups[currectLocIndex]
            activeGroupNumItems = activeGroup.children.length

            if (activeGroupNumItems > 0) {
                scene.add(boxLine)
                activeItemIndex = 0 //reset active item index to 0 (first child of the group)

                activeItem = activeGroup.children[activeItemIndex]
                activeItem.getWorldPosition(target)
                //console.log(activeItem.name, target)
                gsap.to(boxLine.position, { x: target.x, y: target.y, z: target.z, ease: "circle"})
            } else {
                scene.remove(boxLine)
            }

            break

        case 37: //left key

            zoomPanel.visible = false
            zoomPanelOn = false

            //check first if the activegroup still has items that can be selected

            if (activeGroupNumItems > 0 ) {
                scene.add(boxLine)
                if (activeItemIndex === 0) {
                    activeItemIndex = activeGroupNumItems-1
                } else {
                    activeItemIndex--
                }
                activeItem = activeGroup.children[activeItemIndex]
                activeItem.getWorldPosition(target)
                //console.log(activeItem.name, target)
                gsap.to(boxLine.position, { x: target.x, y: target.y, z: target.z, ease: "circle"})
            } else {
                scene.remove(boxLine)
            }
            
			break
		
		case 39: //right key

            //check first if the activegroup still has items that can be selected

            zoomPanel.visible = false
            zoomPanelOn = false

            if (activeGroupNumItems > 0 ) {
                if (activeItemIndex === activeGroupNumItems-1) {
                    activeItemIndex = 0
                } else {
                    activeItemIndex++
                }
                activeItem = activeGroup.children[activeItemIndex]
                activeItem.getWorldPosition(target)
                //console.log(activeItem.name, target)
                gsap.to(boxLine.position, { x: target.x, y: target.y, z: target.z, ease: "circle"})
            } else {
                scene.remove(boxLine)
            } 
            
            break
        
        case 38: //up key 
            
            if (!zoomPanelOn) {
                for (let i in inventoryList) {
                    inventorySlots.children[i].material = new THREE.MeshBasicMaterial(
                        {map: new THREE.TextureLoader().load(previewImgPath + inventoryList[i] + imgType)}
                    ) 
                }

                gsap.to(inventoryGroup.position, { y: camera.position.y+3.5, ease: "circle"})
                inventoryIsUp = true
            
                inventoryNum = inventoryList.length
            }

            break

        case 40: //down key
            
            if (activeGroupNumItems > 0) {
                if (!zoomPanelOn) {
                
                    activeItemZoom = activeItem.name
                    zoomPanel.material.map = new THREE.TextureLoader().load(zoomImgPath + activeItemZoom + imgType)
                    zoomPanel.visible = true
    
                    zoomPanelOn = true
                } else {
                    zoomPanel.visible = false
    
                    zoomPanelOn = false
                }    
            }

            break

        case 13: //enter key
            
            if (!zoomPanelOn) { //while the zoom panel is on, users cannot bag the evidence

                //check first if the activegroup still has items that can be selected
                //if there are more than 1 items, removing selected item will automatically move the selection marker to the next item
                //if there is exactly 1 item in the group, removing that item will also remove the selection marker
                //if there are no more items to select, pressing Enter will prompt alert saying "no more items"

                if (activeGroupNumItems > 1 ) {

                    //limit the number of items user can store in inventory
                    //the number should match the total number of available slots
                    if (inventoryList.length <= 10) { 

                        //save the name of the activeItem to the Inventory List before removing it from the scene
                        inventoryList.push(activeItem.name)

                        placard.position.copy(target)
                        scene.add(placard.clone()) //we clone the placard object so the original can still be used for others

                        activeGroup.remove(activeItem)
                        scene.remove(activeItem)

                        //if the item is the last one on the group list, the box will move to the first item instead (since there's no next one)
                        if (activeItemIndex === activeGroupNumItems-1) {
                            activeItemIndex = 0
                        }
 
                        activeItem = activeGroup.children[activeItemIndex]
                        activeItem.getWorldPosition(target)
                        gsap.to(boxLine.position, { x: target.x, y: target.y, z: target.z, ease: "circle"})

                        activeGroupNumItems = activeGroup.children.length
                    } else {
                        console.log("exceeds inventory slots")
                    }
                    
                } else if (activeGroupNumItems === 1) {
                    
                    //limit the number of items user can store in inventory
                    //the number should match the total number of available slots
                    if (inventoryList.length <= 10) {

                        //save the name of the activeItem to the Inventory List before removing it from the scene
                        inventoryList.push(activeItem.name)
                        placard.position.copy(target)
                        scene.add(placard.clone()) //we clone the placard object so the original can still be used for others

                        activeGroup.remove(activeItem)
                        scene.remove(activeItem)

                        activeGroupNumItems = 0
                        scene.remove(boxLine)
                    } else {
                        console.log("exceeds inventory slots")
                    }

                } else {
                    console.log("no more items!") // to be replaced by on-screen warning
                }
            }

			break
    }
} else if (inventoryIsUp && !observationMode) {

    switch (event.keyCode) {

        case 38: //up key     

        gsap.to(inventoryGroup.position, { y: camera.position.y+5.5, ease: "circle"})
        inventoryIsUp = false
        break

        case 39: //right key

        if (inventoryNum > 0) {
            if (invSelectionIndex === inventoryNum - 1) {
                invSelectionIndex = 0
            } else {
                invSelectionIndex++
            } 
            inventoryMarker.position.x = inventorySlots.children[invSelectionIndex].position.x
        }
        break

        case 37: //left key

        if (inventoryNum > 0) {
            if (invSelectionIndex === 0) {
                invSelectionIndex = inventoryNum - 1
            } else {
                invSelectionIndex--
            } 
            inventoryMarker.position.x = inventorySlots.children[invSelectionIndex].position.x
        }
        break

        case 13: //enter key

        if (inventoryNum > 0) {
            
            let invSelectedItem = inventoryList[invSelectionIndex]

            loader.load(
                path+invSelectedItem+fileType,
                (gltf) =>
                {   
                    observeItem = gltf.scene
                    observeItem.scale.set(2,2,2)
                    scene.add(observeItem)
                }
            )
            observationMode = true
          
        }
        break
    }
} else if (observationMode) {

    switch (event.keyCode) {
        case 13: //enter key
        scene.remove(observeItem)
        observationMode = false
        break

        case 39: //right key
        observeItem.rotateY(0.1)
        break

        case 37: //right key
        observeItem.rotateY(-0.1)
        break

        case 38: //up key
        observeItem.rotateX(-0.1)
        break

        case 40: //down key
        observeItem.rotateX(0.1)
        break
    }
}

}